.link-mobile {
  width: 50px;
  height: 50px;
  box-shadow: 0 0 5px rgba(50, 50, 50, 0.7);
  border-radius: 30px;
  background: #077751;
  bottom: 30px;
  right: 20px;
  z-index: 10000000;
  position: fixed;
  transition: all .2s linear;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  cursor: pointer;

  &:hover {
    background: #077751;
    height: 50px;
    width: 290px;
    border-radius: 30px;
    transition-delay: .2s; }

  span {
    display: block;
    transform: scaleY(0);
    transform-origin: left;
    color: #077751;
    z-index: 4;
    font-size: 22px;
    transition: all .1s linear; }

  &:hover span {
    display: block;
    z-index: 4;
    color: #fff;
    font-size: 20px;
    transform: scaleY(1);
    transition-delay: .4s; } }

.link-mobile__icon {
  position: absolute;
  left: 15px;
  top: 15px;
  width: 20px;
  height: auto;
  margin-right: 50px;
  transition-delay: .3s;
  transition: all 0.5s ease; }

.link-mobile:hover .link-mobile__icon {
  transition-delay: .3s;
  display: none; }

